"use client";
import { usePathname } from 'next/navigation';
import { useAppStore } from './store';
import { useMemo } from 'react';

export type PageType = 'dashboard' | 'clients' | 'routine' | 'production' | 'financial' | 'strategy' | 'tools';

export interface PageContext {
    page: PageType;
    title: string;
    icon: string;
    data: any;
    suggestions: string[];
    quickActions: QuickAction[];
}

export interface QuickAction {
    id: string;
    label: string;
    icon: string;
    prompt: string;
}

export function usePageContext(): PageContext {
    const pathname = usePathname();
    const { clients, tasks, transactions } = useAppStore();

    return useMemo(() => {
        // Dashboard
        if (pathname === '/') {
            const activeClients = clients.filter(c => c.status === 'ativo').length;
            const revenue = transactions
                .filter(t => t.type === 'receita')
                .reduce((sum, t) => sum + t.amount, 0);

            return {
                page: 'dashboard',
                title: 'Dashboard',
                icon: '',
                data: {
                    activeClients,
                    revenue,
                    totalClients: clients.length,
                    totalTasks: tasks.length
                },
                suggestions: [
                    activeClients < 5 ? ' Poucos clientes ativos. Considere prospeco.' : '',
                    revenue < 10000 ? ' Receita baixa este ms. Foque em vendas!' : ''
                ].filter(Boolean),
                quickActions: [
                    { id: 'analyze', label: 'Analisar Performance', icon: '', prompt: 'Analise o desempenho geral da agncia e sugira melhorias' },
                    { id: 'report', label: 'Gerar Relatrio', icon: '', prompt: 'Gere um relatrio executivo do ms atual' },
                    { id: 'suggestions', label: 'Sugestes', icon: '', prompt: 'D 5 sugestes acionveis para melhorar resultados' }
                ]
            };
        }

        // Clients
        if (pathname === '/clients') {
            const inactiveClients = clients.filter(c => c.status === 'inativo').length;

            return {
                page: 'clients',
                title: 'Clientes',
                icon: '',
                data: {
                    totalClients: clients.length,
                    inactiveClients,
                    clients: clients.slice(0, 5) // Top 5 para contexto
                },
                suggestions: [
                    inactiveClients > 0 ? ` ${inactiveClients} clientes inativos. Reativar?` : ''
                ].filter(Boolean),
                quickActions: [
                    { id: 'contact', label: 'Quem Contactar Hoje', icon: '', prompt: 'Liste os 3 clientes que devo contactar hoje baseado em quando foi o ltimo contato' },
                    { id: 'upsell', label: 'Oportunidades Upsell', icon: '', prompt: 'Analise quais clientes tm maior potencial de upsell e novos servios' },
                    { id: 'risk', label: 'Clientes em Risco', icon: '', prompt: 'Identifique clientes em risco de churn e sugira aes' }
                ]
            };
        }

        // Routine
        if (pathname === '/routine') {
            return {
                page: 'routine',
                title: 'Rotina',
                icon: '',
                data: {
                    tasks: tasks.length
                },
                suggestions: [],
                quickActions: [
                    { id: 'prioritize', label: 'Priorizar Tasks', icon: '', prompt: 'Me ajude a priorizar minhas tarefas de hoje' },
                    { id: 'next', label: 'Prximas Aes', icon: '', prompt: 'Quais as 3 prximas aes mais importantes que devo fazer?' },
                    { id: 'optimize', label: 'Otimizar Agenda', icon: '', prompt: 'Como otimizar minha agenda para ser mais produtivo hoje?' }
                ]
            };
        }

        // Production
        if (pathname === '/production') {
            const activeTasks = tasks.filter(t => t.stage !== 'done');

            return {
                page: 'production',
                title: 'Produo',
                icon: '',
                data: {
                    tasks: activeTasks,
                    totalTasks: tasks.length
                },
                suggestions: [
                    activeTasks.length > 10 ? ' Muitos projetos em andamento. Priorize!' : ''
                ].filter(Boolean),
                quickActions: [
                    { id: 'creative', label: 'Ideias Criativas', icon: '', prompt: 'Gere 5 ideias criativas para contedo de redes sociais' },
                    { id: 'briefing', label: 'Gerar Briefing', icon: '', prompt: 'Crie um template de briefing criativo completo' },
                    { id: 'copy', label: 'Escrever Copy', icon: '', prompt: 'Escreva uma copy persuasiva para um anncio de produto' }
                ]
            };
        }

        // Financial
        if (pathname === '/financial') {
            const revenue = transactions
                .filter(t => t.type === 'receita')
                .reduce((sum, t) => sum + t.amount, 0);
            const expenses = transactions
                .filter(t => t.type === 'despesa')
                .reduce((sum, t) => sum + t.amount, 0);

            return {
                page: 'financial',
                title: 'Financeiro',
                icon: '',
                data: {
                    revenue,
                    expenses,
                    profit: revenue - expenses,
                    transactions: transactions.length
                },
                suggestions: [
                    expenses > revenue ? ' Despesas maiores que receita! Ateno!' : ''
                ].filter(Boolean),
                quickActions: [
                    { id: 'reduce', label: 'Reduzir Custos', icon: '', prompt: 'Analise minhas despesas e sugira onde posso reduzir custos' },
                    { id: 'forecast', label: 'Previso de Receita', icon: '', prompt: 'Preveja a receita dos prximos 3 meses baseado no histrico' },
                    { id: 'analyze', label: 'Anlise Completa', icon: '', prompt: 'Faa uma anlise financeira completa e sugira melhorias' }
                ]
            };
        }

        // Strategy (j tem chat prprio)
        if (pathname === '/strategy') {
            return {
                page: 'strategy',
                title: 'Estratgia IA',
                icon: '',
                data: {},
                suggestions: [' Esta pgina tem chat IA dedicado'],
                quickActions: []
            };
        }

        // Default
        return {
            page: 'dashboard',
            title: 'OPS Operation',
            icon: '',
            data: {},
            suggestions: [],
            quickActions: []
        };
    }, [pathname, clients, tasks, transactions]);
}
